.container{
  padding-left: 15px;
  padding-right: 15px;
  margin-right: auto;
  margin-left: auto;
  /* margin: 0 10px; */
}

.titleWrapper{
  padding-top: 50px;
  padding-bottom: 20px;
}

.title{
  height: 115px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Evolventa';
  font-weight: 700;
  font-size: 28px;
  letter-spacing: 1.3px;
  line-height: 35px;
  text-transform: uppercase;

  color: #FFFFFF;
  transition: 1s;
}


.AppVertical{
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
}

