  .title{
    font-size: 52px;
    line-height: 85px;
    letter-spacing: 2px;
    }

} */


@media screen and (min-width: 768px) {
  .titleWrapper{
    padding-top: 100px;
    padding-bottom: 80px;
  }

  .title{
    height: 170px;
    font-size: 52px;
    line-height: 85px;
    letter-spacing: 2px;
    }

}

@media screen and (min-width: 1024px) {
  .titleWrapper{
    padding-bottom: 20px;
  }
}

@media screen and (min-width: 1366px) {
  .titleWrapper{
    padding-top: 20px;
    padding-bottom: 70px;
  }

  .title{
    height: 85px;
    font-size: 52px;
    line-height: 85px;
    letter-spacing: 2px;
    }

}

@media screen and (min-width: 1920px) {
  .titleWrapper{
    padding-top: 140px;
    padding-bottom: 100px;
  }

  .title{
    font-size: 64px;
    line-height: 85px;
    letter-spacing: 2px;
    }
    
}

@font-face {
  font-family: 'Evolventa';
  src: local('Evolventa-Bold'), local('Evolventa-Bold'),
      url('./fonts/Evolventa/Evolventa-Bold.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Evolventa';
  src: local('Evolventa-BoldObligue'), local('Evolventa-BoldObligue'),
      url('./fonts/Evolventa/Evolventa-BoldOblique.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

